







let importAll = (requireContext: __WebpackModuleApi.RequireContext) => requireContext.keys().forEach(requireContext); //这里ts语言不认识requireContext，需要明确其类型
try {
  importAll(require.context("../assets/icons", true, /\.svg$/));
} catch (err) {
  console.log(err);
}
export default {
    props:['name'],
    //外部传入name的值来决定icon图标
    name:'Icon', 
}
