






import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
@Component
export default class Types extends Vue{ 
    @Prop()readonly type!:string; 
    selectType(type: string):void{
        if(type!=='-' && type!=='+'){
            throw new Error('type is unknown');           
        }
        this.$emit('update:type', type)
    }
}
